<template>
  <div class="hello">
    <header>
      <h4 style="margin: 5px">儿童视频管理后台</h4>
      <div>
        <span class="bt" @click="sendEvent('input keyevent 24')">音量+</span>
        <span class="bt" @click="sendEvent('input keyevent 25')">音量-</span>
        <span class="bt" @click="sendEvent('input keyevent 23')">暂停</span>
        <span class="bt" @click="sendEvent('adb shell input keyevent 4')"
          >返回</span
        >
        <div style="float: left">
          MODE:
          <span class="bt">顺序</span>
          <span class="bt">随机</span>
          <span class="bt">单个</span>
        </div>
      </div>
    </header>
    <div>
      <ul>
        <li class="it" v-for="(it, k) in items" :key="it.aid">
          <img
            class="coverUrl"
            :src="it.coverUrl"
            referrerPolicy="no-referrer"
          />
          <div>
            <div>{{ it.title || it.aid }}({{ it.aid }})</div>
            <div>
              <span
                :class="{ cur: aIndex == k && bIndex == i }"
                @click="play(k, i)"
                class="url"
                v-for="(item, i) in it.items"
                :key="item.path"
                :title="item.title"
                >{{ it.title && it.title != "dy" ? i + 1 : item.title }}</span
              >
            </div>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  name: "Main",
  data: () => {
    return {
      items: [],
      aIndex: 0,
      bIndex: 0,
    };
  },
  mounted: function () {
    this.getDatas();

    setInterval(() => {
      this.getStatus();
    }, 30000);
  },
  props: {
    msg: String,
  },
  methods: {
    base64(item) {
      let n = "data:image/png;base64," + item.thumb;
      console.log(n);
      return n;
    },
    sendEvent(str) {
      fetch("/api/event", {
        method: "post",
        body: JSON.stringify({ event: str }),
        headers: {
          "Content-type": "application/x-www-form-urlencoded",
        },
      })
        .then((data) => {
          return data.text();
        })
        .then((val) => {
          console.log(val); //  输出请求到的字符串
        });
    },
    play(k, i) {
      this.aIndex = k;
      this.bIndex = i;
      fetch("/api/play", {
        method: "post",
        body: JSON.stringify({ aIndex: k, bIndex: i }),
        headers: {
          "Content-type": "application/x-www-form-urlencoded",
        },
      })
        .then((data) => {
          return data.text();
        })
        .then((val) => {
          console.log(val); //  输出请求到的字符串
        });
    },
    addOrUpdate(item) {
      fetch("/api/addOrUpdate", {
        method: "post",
        body: JSON.stringify(item),
        headers: {
          "Content-type": "application/x-www-form-urlencoded",
        },
      })
        .then((data) => {
          return data.text();
        })
        .then((val) => {
          console.log(val); //  输出请求到的字符串
        });
    },
    remove() {
      fetch(
        "/api/delete?ids=" +
          this.items
            .filter((e) => e.checked)
            .map((e) => e.id)
            .join(",")
      ).then(() => {
        var that = this;
        for (var i = that.items.length - 1; i >= 0; i--) {
          var item = that.items[i];
          if (item.checked) {
            that.items.splice(i, 1);
          }
        }
      });
    },
    getDatas() {
      fetch("/api/list.json", {
        method: "GET",
        mode: "cors",
        credentials: "include",
      })
        .then((response) => {
          response.json().then((data) => {
            this.items.length = 0;
            this.items.push(...data);
          });
        })
        .catch(function (e) {
          console.log("error: " + e.toString());
        });
    },

    reload() {
      fetch("/api/reloadplaylist", {
        method: "GET",
      })
        .then((response) => {
          response.json().then((data) => {
            this.items.length = 0;
            this.items.push(...data);
          });
        })
        .catch(function (e) {
          console.log("error: " + e.toString());
        });
    },
    getStatus() {
      fetch("/api/status", {
        method: "GET",
      })
        .then((response) => {
          response.json().then((data) => {
            Object.assign(this, data);
          });
        })
        .catch(function (e) {
          console.log("error: " + e.toString());
        });
    },
  },
};
</script>

