  display: flex;
  text-align: left;

  padding-top: 10px;
}
.coverUrl {
  width: 100px;
  height: 100px;
}
.url {
  margin: 5px;
  float: left;
  cursor: pointer;
}
.cur {
  background: gray;
}
.bt {
  cursor: pointer;
  margin: 5px 3px;
}
</style>
